export default {
	data() {
		return {
			tabList:['全部','进行中','已结束'],
			n:0,
			page:1,
			loading:false,
			hotList:[],
			allowloading:false,
			showTitle:false
		}
	},
	onLoad() {
		this.getActivity()
	},
	onPullDownRefresh(){
		this.page = 1
		this.getActivity()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	},
	onReachBottom(){
		if(this.allowloading){
			this.allowloading = false
			this.loading = true
			this.page++
			this.getActivity()
			// setTimeout(() => {
			// 	this.page++
			// 	this.getActivity()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
	},
	methods: {
		goDetail(id){
			uni.navigateTo({
				url:'../../pagesTwo/ActivityDetail/ActivityDetail?id='+id
			})
		},
		getActivity () {
		  if (this.n === 0) {
		    this.getAll()
		  } else if (this.n === 1) {
		    this.getUnderway()
		  }else{
			 this.getFinish() 
		  }
		},
		tab(index){
			this.n = index
			this.page = 1 
			this.getActivity()
		},
		// 全部
		getAll(){
			this.$api.my_activity({
			  token: uni.getStorageSync("token"),
			  user_id: uni.getStorageSync("userId"),
			  page: this.page
			}).then(res => {
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.page===1){
						this.hotList = res.data.data
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}else{
						this.hotList = this.hotList.concat(res.data.data)
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}
					
					for (let j = 0; j < this.hotList.length; j++) {
					  if (new Date(this.hotList[j].finish_date.replace(/-/g, '/')).getTime() > this.time) { // 结束时间大于当前时间
					    this.hotList[j].state = true
					  } else {
					    this.hotList[j].state = false
					  }
					}
					
				}
			})
		},
		// 进行中
		getUnderway(){
			this.$api.my_activity_ing({
			  token: uni.getStorageSync("token"),
			  user_id: uni.getStorageSync("userId"),
			  page: this.page
			}).then(res => {
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.page===1){
						this.hotList = res.data.data
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}else{
						this.hotList = this.hotList.concat(res.data.data)
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}
					
					for (let j = 0; j < this.hotList.length; j++) {
					  if (new Date(this.hotList[j].finish_date.replace(/-/g, '/')).getTime() > this.time) { // 结束时间大于当前时间
					    this.hotList[j].state = true
					  } else {
					    this.hotList[j].state = false
					  }
					}
					
				}
			})
		},
		// 已结束
		getFinish(){
			this.$api.my_activity_end({
			  token: uni.getStorageSync("token"),
			  user_id: uni.getStorageSync("userId"),
			  page: this.page
			}).then(res => {
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.page===1){
						this.hotList = res.data.data
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}else{
						this.hotList = this.hotList.concat(res.data.data)
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}
					
					for (let j = 0; j < this.hotList.length; j++) {
					  if (new Date(this.hotList[j].finish_date.replace(/-/g, '/')).getTime() > this.time) { // 结束时间大于当前时间
					    this.hotList[j].state = true
					  } else {
					    this.hotList[j].state = false
					  }
					}
					
				}
			})
		},
	}
}
